import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import HeaderCard from "../../HeaderCard";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { renderField, renderFieldSelect } from "../../renderField";
import { getcustomerList } from "../../actions/customerAction";
import { AxiosMasterGet } from "../../Axios";
import PrintNota from "../../Stocking/Component/PrintNota";
import { checkLaporan, formatGram, formatRp } from "../../helper";
import { getToday } from "../../GlobalComponent/function";

class FormLaporanReturnCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal_invoice",
          text: "Tanggal",
        },

        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_ext",
          text: "Total Ext(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Total Netto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_rp",
          text: "Total Rp(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total",
          text: "Total(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.getDetails(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: "ada",
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };
  caridata() {
    AxiosMasterGet(
      `report/retur-customer/DETAIL&${this.props.kode_customer}&${this.props.tanggal_awal}&${this.props.tanggal_akhir}`
    ).then((res) => {
      if (checkLaporan(res.data)) {
        this.setState({
          hasilDataGet: res.data && res.data[0].detail,
        });
      }
    });
  }

  getDetails(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data.detail_barang;
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kadar_cetak,
        ticket.kode_jenis,
        formatGram(ticket.bruto),
        ticket.kadar_jual,
        formatGram(ticket.disc),
        formatGram(ticket.netto, 3),
        formatGram(ticket.pkg),
        ticket.qty,
        formatRp(ticket.ongkos_rp),
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      formatGram(
        table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
      ),
      "",
      formatGram(
        table.map((list) => parseFloat(list.disc)).reduce((a, b) => a + b, 0)
      ),
      formatGram(
        table.map((list) => parseFloat(list.netto)).reduce((a, b) => a + b, 0),
        3
      ),
      formatGram(
        table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0)
      ),
      table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
      formatRp(
        table
          .map((list) => parseFloat(list.ongkos_rp))
          .reduce((a, b) => a + b, 0)
      ),
      this.state.totalNWExt,
    ];
    tableRows.push(footer);
    var columTable = [
      "No",
      "Kadar",
      "Kode",
      "Bruto \n(Gr)",
      "Harga \n(%)",
      "Diskon",
      "Netto \n(Gr)",
      "Pkg",
      "Qty",
      "Ongkos \n(Rp)",
      "Grand Total\nNetto Ext",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "No Bon",
      data.no_bon.toUpperCase(),
      "Tanggal",
      data.tanggal_invoice,
      "",
      "",
      "",
      "",
      "ADMIN",
      this.state.tanggal,
      "",
      columTable,
      "Return Transaksi Penjualan",
      tableRows
    );
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Return Customer" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-4">
                  <Field
                    name="kode_customer"
                    component={renderFieldSelect}
                    options={this.props.customer.map((data) => {
                      let hasil = {
                        value: data.kode_customer,
                        name: data.nama_customer,
                      };
                      return hasil;
                    })}
                    type="text"
                    label="Nama Customer"
                    placeholder="Masukan Nama Customer"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="tanggal_awal"
                    component={renderField}
                    type="date"
                    label="Mulai Dari"
                    placeholder="Masukan Mulai Dari"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="tanggal_akhir"
                    component={renderField}
                    type="date"
                    label="Sampai Dengan"
                    placeholder="Masukan Sampai Dengan"
                  />
                </div>
                <div className="col-md-12 col-lg-12 mt-3">
                  <div className="row">
                    <div className="col-lg-6 col-md-6">
                      <div className="text-left">
                        {/* <Link to="/ViewPDF">
                              <button className="btn btn-primary" type="button">
                                PDF
                              </button>
                            </Link> */}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.caridata()}
                        >
                          <i className="fa fa-search mr-1"></i>
                          Cari Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="no_bon"
                        data={this.state.hasilDataGet}
                        columns={this.state.columns}
                        search
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

FormLaporanReturnCustomer = reduxForm({
  form: "FormLaporanReturnCustomer",
})(FormLaporanReturnCustomer);
const selector = formValueSelector("FormLaporanReturnCustomer");
export default connect((state) => {
  const { kode_customer, tanggal_awal, tanggal_akhir } = selector(
    state,
    "kode_customer",
    "tanggal_awal",
    "tanggal_akhir"
  );
  return {
    customer: state.provinsi.customer,
    kode_customer: kode_customer,
    tanggal_akhir: tanggal_akhir,
    tanggal_awal: tanggal_awal,
    initialValues: {
      tanggal_awal: getToday(),
      tanggal_akhir: getToday(),
    },
  };
})(FormLaporanReturnCustomer);
